import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow as tomorrowStyle } from 'react-syntax-highlighter/dist/cjs/styles/prism';
import type { ComponentPropsWithoutRef } from 'react';
import remarkGfm from 'remark-gfm';

interface CodeProps extends ComponentPropsWithoutRef<'code'> {
  inline?: boolean;
}

interface ChatMessagesProps {
  messages: Array<{
    role: 'user' | 'assistant';
    content: string;
  }>;
}

export default function ChatMessages({ messages }: ChatMessagesProps) {
  return (
    <div className="h-full px-4">
      <div className="max-w-3xl mx-auto w-full py-4 space-y-4">
        {messages.length === 0 ? (
          <div className="flex items-center justify-center h-full">
            <div className="text-center text-gray-500 dark:text-gray-400">
              <div className="text-4xl mb-4">💬</div>
              <div className="text-xl font-medium">채팅을 시작하세요</div>
              <div className="mt-2">질문을 입력하시면 AI가 답변해드립니다</div>
            </div>
          </div>
        ) : (
          messages.map((message, index) => (
            <div
              key={index}
              className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
            >
              <div
                className={`max-w-[80%] p-4 rounded-lg whitespace-pre-wrap ${
                  message.role === 'user'
                    ? 'bg-blue-500 text-white'
                    : 'bg-gray-100 dark:bg-gray-800 text-gray-900 dark:text-gray-200'
                }`}
              >
                {message.content}
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
} 