import { useState, useEffect } from 'react';
import Header from '@/components/common/Header';
import Sidebar from '@/components/ai-search/Sidebar';
import ChatInput from '@/components/ai-search/ChatInput';
import ChatMessages from '@/components/ai-search/ChatMessages';

interface Message {
  role: 'user' | 'assistant';
  content: string;
}

interface Chat {
  id: string;
  title: string;
  date: string;
  messages: Message[];
}

export default function AISearch() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [chats, setChats] = useState<Chat[]>([]);
  const [currentChatId, setCurrentChatId] = useState<string | null>(null);

  // 로컬 스토리지에서 채팅 데이터 로드
  useEffect(() => {
    const savedChats = localStorage.getItem('chats');
    if (savedChats) {
      setChats(JSON.parse(savedChats) as Chat[]);
    }
  }, []);

  // 채팅 데이터가 변경될 때마다 로컬 스토리지에 저장
  useEffect(() => {
    localStorage.setItem('chats', JSON.stringify(chats));
  }, [chats]);

  // 현재 채팅이 변경될 때 해당 채팅의 메시지 로드
  useEffect(() => {
    if (currentChatId) {
      const currentChat = chats.find(chat => chat.id === currentChatId);
      if (currentChat) {
        setMessages(currentChat.messages);
      }
    } else {
      setMessages([]);
    }
  }, [currentChatId, chats]);

  const createNewChat = (initialMessage?: string) => {
    const newChat: Chat = {
      id: Date.now().toString(),
      title: initialMessage?.slice(0, 30) || '새로운 채팅',
      date: new Date().toISOString().split('T')[0],
      messages: []
    };
    setChats(prev => [newChat, ...prev]);
    setCurrentChatId(newChat.id);
    setMessages([]);
    return newChat.id;
  };

  const updateChatMessages = (chatId: string, newMessages: Message[]) => {
    setChats(prev => prev.map(chat => 
      chat.id === chatId 
        ? { ...chat, messages: newMessages }
        : chat
    ));
  };

  const handleSendMessage = async (message: string) => {
    try {
      setIsLoading(true);
      
      // 현재 채팅이 없으면 새로 생성
      if (!currentChatId) {
        const newChatId = createNewChat(message);
        setCurrentChatId(newChatId);
      }

      // 사용자 메시지 추가
      const updatedMessages: Message[] = [...messages, { role: 'user', content: message }];
      setMessages(updatedMessages);
      updateChatMessages(currentChatId!, updatedMessages);

      const response = await fetch('http://localhost:8000/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'text/event-stream',
        },
        body: JSON.stringify({ message }),
      });

      if (!response.ok) throw new Error('Network response was not ok');
      
      const assistantMessageIndex = updatedMessages.length;
      const newMessages: Message[] = [...updatedMessages, { role: 'assistant', content: '' }];
      setMessages(newMessages);
      updateChatMessages(currentChatId!, newMessages);

      const reader = response.body?.getReader();
      if (!reader) throw new Error('Reader not available');

      let fullContent = '';
      while (true) {
        const { done, value } = await reader.read();
        if (done) break;
        
        const text = new TextDecoder().decode(value);
        fullContent += text;
        
        const latestMessages: Message[] = [...updatedMessages, { role: 'assistant', content: fullContent }];
        setMessages(latestMessages);
        updateChatMessages(currentChatId!, latestMessages);
      }
    } catch (error) {
      console.error('Error:', error);
      alert('서버에 연결할 수 없습니다. 관리자에게 문의하세요.');
      const revertedMessages = messages.slice(0, -1);
      setMessages(revertedMessages);
      if (currentChatId) {
        updateChatMessages(currentChatId, revertedMessages);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const updateChatTitle = (chatId: string, newTitle: string) => {
    setChats(prev => prev.map(chat => 
      chat.id === chatId 
        ? { ...chat, title: newTitle }
        : chat
    ));
  };

  const handleSelectChat = (chatId: string) => {
    setCurrentChatId(chatId);
    const selectedChat = chats.find(chat => chat.id === chatId);
    if (selectedChat) {
      setMessages(selectedChat.messages);
    }
  };

  return (
    <div className="min-h-screen flex flex-col bg-white dark:bg-gray-900">
      <Header />
      
      <div className="flex flex-1 pt-16">
        <div className="w-64 bg-gray-50 dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700">
          <Sidebar 
            chats={chats}
            currentChatId={currentChatId}
            onNewChat={() => createNewChat()}
            onSelectChat={handleSelectChat}
            onUpdateTitle={updateChatTitle}
          />
        </div>
        
        <main className="flex-1 flex flex-col justify-between">
          <div className="flex-grow">
            <ChatMessages messages={messages} />
          </div>
          
          <div className="p-4 border-t border-gray-200 dark:border-gray-700 bg-white dark:bg-gray-900">
            <ChatInput onSendMessage={handleSendMessage} disabled={isLoading} />
          </div>
        </main>
      </div>
    </div>
  );
} 